@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  font-family: 'Montserrat', sans-serif;
  margin: 0;
  padding: 0;
}

.mainBody {
  width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.mainBody h1 {
  background-color: lightcoral;
  width: 100vw;
  height: 12vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 3rem;
  letter-spacing: 2px;
  color: white;
  margin-bottom: 2vh;
}

.mainBody form {
  width: 100vw;
  height: 5vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 5vh;
}

.mainBody form input {
  outline: none;
  border: 0;
  width: 50vw;
  height: 4vh;
  font-size: 1.5rem;
  transition: width .2s ease;
  border-bottom: 2px solid lightcoral;
}

.mainBody form button {
  outline: 2px solid lightcoral;
  min-width: 10vw;
  height: 4vh;
  background-color: white;
  border: 0;
  margin-left: 2vw;
  transition: background-color .2s ease;
  cursor: pointer;
}

.mainBody form button:hover {
  color: white;
  background-color: lightcoral;
}

.mainBody .card {
  width: 70vw;
  min-height: 50vh;
  background-color: rgba(255, 160, 122, 0.452);
  margin-bottom: 4vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.mainBody .card h2 {
  margin-top: 2vh;
  margin-bottom: 4vh;
  cursor: pointer;
}

.mainBody .card img {
  border-radius: 50%;
  margin-bottom: 2vh;
  cursor: pointer;
}

.mainBody .card h4 {
  text-align: left;
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.mainBody .card p {
  margin-bottom: .8rem;
  margin-left: .5rem;
}

.mainBody .card a {
  text-decoration: none;
  padding: .5rem 2rem;
  border-radius: 1rem;
  background-color: lightsalmon;
  color: white;
}

.mainBody .card a:hover {
  background-color: #ff6426;
}

.mainBody .card h3 {
  margin-bottom: 4vh;
  margin-top: 3vh;
}
